.LbRectangular {
    text-align: center;
    margin: auto;
    display: table;
}

.LbRectangular-label{
    display: table-cell;
    vertical-align: middle;
    display: inline-block;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: auto 70%;
    position: relative;
}

.LbRectangular-input {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 25px;
    height: 25px;
    border:2px solid rgba(163,40,233,1);
}

.LbRectangular-title {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    width: 200px;
    max-width: 100%;
}